.profile__avatar-overlay {
    background-color: rgba(0, 0, 0, 0.8);
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.profile__avatar:hover .profile__avatar-overlay {
    opacity: 1;
}