.popup {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    opacity: 0;
    visibility: hidden;
    transition: visibility 0.3s, opacity 0.3s linear;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}